<template>
  <div class="title">
    <div id="wrapper" v-bind:style="{'top': wrapHeight}">
        <vue-particles
          color="#587792"
          :particleOpacity="1.0"
          :particlesNumber="38"
          shapeType="circle"
          :particleSize="9"
          linesColor="8db1ab"
          :linesWidth="2"
          :lineLinked="true"
          :lineOpacity="1.0"
          :linesDistance="170"
          :moveSpeed="0.75"
          :hoverEffect="true"
          hoverMode="grab"
          :clickEffect="true"
          clickMode="push"
        ></vue-particles>
        <div id="text" v-bind:style="{'top': textHeight}">
          <div id="first" >Vishnu</div>
          <div id="last">Rengaraj</div>
          <div id="subtitle">{{ whois[0] }}</div>
        </div>
     


        <button id="testButton" v-on:click="cons()"></button>

        <button id="learn">
          <div class="arrow">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </button>

        </div>

   <div id="contents" v-bind:style="{'top': pad}">
           <Resume />
       

        
        <Project />
   </div>
  
        
  </div>
</template>

<script>
import Resume from "./Resume.vue";
import Project from "./Project.vue";
//var VueScrollTo = require('vue-scrollto');


export default {
  name: "Title",
  components: {
    Resume,
    Project
  },

  data() {
    return {
      pad: '100vh',
      textHeight: '20%',
      wrapHeight: '0px',
      whois: [
        "Mechanical Engineer",
        "Computer Scientist",
        "Graduate Student",
        "Designer",
        "CAE Engineer",
        "Stress Analyst",
        "Banana Lover",
        "Michigan Alumnus",
        "Test Engineer",
        "Master of Eggs"
      ]
    };
  },
  mounted() {
    var vm = this;
    window.setInterval(() => {
      this.pollPerson();
    }, 2700);
    window.addEventListener('scroll', function(){
      var scrollPos = window.scrollY
      var winHeight = window.innerHeight
      //var docHeight = document.documentElement.scrollHeight // instead document.body.clientHeight
      var perc = 100 - (25 * scrollPos / (winHeight))
      vm.pad = (perc+'vh')
      
      var t = 20 - (6 * scrollPos / (winHeight))
      vm.textHeight = (t+'%')

      var w = 0 - (100 * scrollPos / (winHeight))
      vm.wrapHeight = (w+'px')


      
    })
    
    
  },
  computed: {

  },
  methods: {
    clear: function() {
      this.isActiveResume = false;
      this.isActiveProject = false; 
    },
    cons(x) {
      console.log('cons' + x)
    },

    pollPerson() {
      const first = this.whois.shift();
      this.whois = this.whois.concat(first);
    }
  }
};
</script>

<style scoped>


.arrow {
  z-index: 2;
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.arrow span {
  display: block;
  width: 4vw;
  height: 4vw;
  border-bottom: 3px solid #1f2833;
  border-right: 3px solid #1f2833;
  transform: rotate(45deg);
  margin: -10px;
  animation: arrows 2s infinite;
}
.arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.arrow span:nth-child(3) {
  animation-delay: -0.4s;
}
@keyframes arrows {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}

#pull {
  background-color: #85ffd8;
  position: absolute;
  width: 10%;
  left: 50%;
  bottom: 0%;

  z-index: 7;
  height: 6%;
  transition: all 800ms ease-in-out; /*cubic-bezier(0.075, 0.82, 0.165, 1);*/
}

#testButton {
  position: absolute;
  right: 5%;
  top: 5%;
  z-index: 99;
}



#wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  z-index: 0;

}

#contents {
  z-index: 99;
  background-color: white;
    box-shadow: 0 -14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  position: relative;
  top: 100vh;
  left: 0;
  width: 100%;
  height: 100%;
}


#particles-js {
  background-image: linear-gradient(white , #fafafa);
  background-color: #1f2833;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#text {
  position: absolute;
  top: 20%;
  left: 20%;

  /*transform: translate(-75%, -50%);*/

  height: 40vh;
  z-index: 2;
}

#first,
#last,
#subtitle {
  --pad: 5vw;
  text-align: left;
  letter-spacing: 7px;
  font-size: 9vw;
  color: #1f2833;
  padding: var(--pad);
  padding-left: 0;
  font-weight: 600;
  display: block;
  z-index: 2;
  /*
  background-color: black;*/
}

#subtitle {
  font-size: 4.5vw;
  padding: 2.5vw;
  padding-left: 0;
  font-weight: 400;
  
}

@media screen and (min-width: 1000px) {
  #first,
  #last {
    font-size: 90px;
    padding: 50px;
    padding-left: 0;
  }

  #subtitle {
    font-size: 45px;
    padding: 25px;
    padding-left: 0;
  }

  #learn {
    bottom: 120px;
  }

  .arrow span {
    width: 30px;
    height: 30px;
  }
}
</style>
